* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  height: 100vh;
  color: #fff;
  background-color: #242333;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Lato, sans-serif;
  display: flex;
}

.sections {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-around;
  align-items: start;
  margin: 2rem;
  padding: 2rem;
  display: grid;
}

section {
  margin: 0 3rem;
}

h3 {
  font-size: 2.4rem;
}

ul {
  margin: 2rem 0;
  list-style: none;
}

form {
  justify-content: center;
  align-items: center;
  display: flex;
}

input {
  width: 18rem;
  height: 3.6rem;
  color: #242333;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin: .8rem 0;
  padding: 1rem;
  font-size: 1.4rem;
}

input:focus {
  outline: none;
}

input:disabled {
  color: #242333;
  background-color: #e4fdff;
}

form .el__tools {
  width: 5rem;
  height: 3.6rem;
  color: #242333;
  background-color: #fff;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: center;
  align-items: center;
  gap: .6rem;
  display: flex;
}

form .el__tool {
  cursor: pointer;
  font-size: 1.6rem;
}

button {
  color: #242333;
  cursor: pointer;
  width: 23rem;
  background-color: #6feaf6;
  border: none;
  border-radius: 5px;
  margin-top: .9rem;
  padding: 1rem 0;
  font-weight: 600;
}

.hold {
  opacity: .5;
}

.invisible {
  display: none;
}

.droppable {
  background-color: #2a3e55;
  border-radius: 9px;
}

@media (max-width: 60em) {
  .sections {
    grid-template-columns: 1fr;
    row-gap: 4rem;
  }

  input {
    width: 50rem;
  }

  button {
    width: 55rem;
  }
}

@media (max-width: 43em) {
  input {
    width: 30rem;
  }

  button {
    width: 35rem;
  }
}

@media (max-width: 31em) {
  input {
    width: 18rem;
  }

  button {
    width: 23rem;
  }
}

